.med-list {
    width: 100%;
    max-width: 360px;
    padding: 6px;
    background: #EEE;
    border-radius: 0px;
}

@media screen and (min-width: 768px) {
    .med-list {
        box-shadow: 0px 3px 3px -2px rgba(0,0,0,0.2),0px 3px 4px 0px rgba(0,0,0,0.14),0px 1px 8px 0px rgba(0,0,0,0.12)
    }
}